.app {
  max-width: 500px;
  margin: 50px auto;
  text-align: center;
  font-family: Arial, sans-serif;
}

.input-section {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

input {
  padding: 8px;
  width: 70%;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 8px 12px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
}

button:hover {
  background-color: #0056b3;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 8px 0;
  padding: 8px;
  background: #f4f4f4;
  border-radius: 4px;
}

li.completed span {
  text-decoration: line-through;
  color: gray;
}

textarea {
  width: 100%;
  margin-top: 10px;
  padding: 8px;
  font-family: monospace;
  border: 1px solid #ccc;
  border-radius: 4px;
}